@mixin blockWidth {
  max-width: 1440px;
  width: 100%;
}

@mixin contentWidth {
  max-width: 1016px;
  width: 100%;
  margin: 0 auto;
}

@mixin sectionTitle {
  font-size: 70px;
  font-style: normal;
  font-weight: 400;
  line-height: 70px; /* 99.739% */
  letter-spacing: 3px;
  color: $titleTextColor;
  margin: 0 auto 20px;
  white-space: wrap;
}
